'use client'

import { useOS } from '@/context/os-context'
import { useState } from 'react'
import { PinPad } from '@/components/ui/pin-pad'
import { Lock, Shield, Wifi, RefreshCw, Check, Smartphone } from 'lucide-react'

export const Settings = () => {
  const { 
    theme, setTheme, wallpaper, setWallpaper, darkMode, setDarkMode, 
    credentials, getCredentials, installedApps,
    pin, setPin, isPinVerified, verifyPin,
    wifiEnabled, setWifiEnabled, availableNetworks, connectedNetwork, connectToNetwork, scanNetworks
  } = useOS()
  
  const [activeSection, setActiveSection] = useState('wifi')
  const [showPinPad, setShowPinPad] = useState(false)
  const [pinMode, setPinMode] = useState<'verify' | 'create'>('verify')
  const [wifiPassword, setWifiPassword] = useState('')
  const [selectedNetwork, setSelectedNetwork] = useState<string | null>(null)

  // ... (resto de l√≥gica de credenciales igual)
  const handleAccessCredentials = () => {
    if (!pin) { setPinMode('create'); setShowPinPad(true); }
    else if (!isPinVerified) { setPinMode('verify'); setShowPinPad(true); }
  }

  const handleConnectWifi = () => {
    if (selectedNetwork) {
       connectToNetwork(selectedNetwork)
       setSelectedNetwork(null)
       setWifiPassword('')
    }
  }

  return (
    <div className="w-full h-full flex flex-col bg-gray-100 dark:bg-gray-900">
      {/* Barra lateral / superior de navegaci√≥n */}
      <div className="flex gap-0 bg-white dark:bg-gray-800 border-b dark:border-gray-700 overflow-x-auto scrollbar-hide">
        {[
          { id: 'wifi', icon: Wifi, label: 'Wi-Fi' },
          { id: 'display', icon: Smartphone, label: 'Pantalla' },
          { id: 'credentials', icon: Shield, label: 'Seguridad' },
        ].map((item) => (
          <button
            key={item.id}
            onClick={() => setActiveSection(item.id)}
            className={`flex-1 min-w-[80px] px-2 py-3 text-xs font-semibold flex flex-col items-center gap-1 ${
              activeSection === item.id
                ? 'border-b-2 border-blue-500 text-blue-600 dark:text-blue-400'
                : 'text-gray-500'
            }`}
          >
            <item.icon size={18} />
            {item.label}
          </button>
        ))}
      </div>

      <div className="flex-1 overflow-y-auto p-4">
        {/* SECCI√ìN WI-FI */}
        {activeSection === 'wifi' && (
           <div className="space-y-4">
              <div className="flex items-center justify-between bg-white dark:bg-gray-800 p-4 rounded-xl">
                 <span className="font-medium dark:text-white">Wi-Fi</span>
                 <label className="relative inline-flex items-center cursor-pointer">
                    <input type="checkbox" checked={wifiEnabled} onChange={(e) => setWifiEnabled(e.target.checked)} className="sr-only peer" />
                    <div className="w-11 h-6 bg-gray-200 peer-focus:outline-none rounded-full peer dark:bg-gray-700 peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:left-[2px] after:bg-white after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-blue-600"></div>
                 </label>
              </div>

              {wifiEnabled && (
                 <div className="space-y-2 animate-in slide-in-from-top-2">
                    <div className="flex justify-between items-center px-2">
                       <h3 className="text-sm font-medium text-gray-500 uppercase">Redes Disponibles</h3>
                       <button onClick={scanNetworks} className="p-2 text-blue-500 hover:bg-blue-50 rounded-full">
                          <RefreshCw size={16} />
                       </button>
                    </div>
                    
                    {availableNetworks.length === 0 ? (
                       <div className="text-center py-8 text-gray-400">
                          <p>Buscando redes...</p>
                       </div>
                    ) : (
                       availableNetworks.map((net) => (
                          <div key={net.ssid} className="bg-white dark:bg-gray-800 rounded-xl overflow-hidden border dark:border-gray-700">
                             <button 
                                onClick={() => net.ssid === connectedNetwork ? null : setSelectedNetwork(net.ssid)}
                                className="w-full p-4 flex items-center justify-between hover:bg-gray-50 dark:hover:bg-gray-700"
                             >
                                <div className="flex items-center gap-3">
                                   <Wifi size={20} className={net.ssid === connectedNetwork ? "text-blue-500" : "text-gray-400"} />
                                   <span className={`font-medium ${net.ssid === connectedNetwork ? 'text-blue-500' : 'dark:text-white'}`}>
                                      {net.ssid}
                                   </span>
                                </div>
                                {net.ssid === connectedNetwork && <Check size={18} className="text-blue-500"/>}
                                {net.isSecured && net.ssid !== connectedNetwork && <Lock size={14} className="text-gray-400"/>}
                             </button>
                             
                             {selectedNetwork === net.ssid && net.ssid !== connectedNetwork && (
                                <div className="p-4 bg-gray-50 dark:bg-gray-900 border-t dark:border-gray-700 animate-in slide-in-from-top">
                                   <input 
                                      type="password" 
                                      placeholder="Contrase√±a"
                                      value={wifiPassword}
                                      onChange={(e) => setWifiPassword(e.target.value)}
                                      className="w-full p-2 mb-2 border rounded dark:bg-gray-800 dark:text-white text-sm"
                                   />
                                   <div className="flex gap-2">
                                      <button onClick={handleConnectWifi} className="flex-1 bg-blue-500 text-white py-2 rounded text-sm font-bold">Conectar</button>
                                      <button onClick={() => setSelectedNetwork(null)} className="flex-1 bg-gray-300 dark:bg-gray-700 py-2 rounded text-sm">Cancelar</button>
                                   </div>
                                </div>
                             )}
                          </div>
                       ))
                    )}
                 </div>
              )}
           </div>
        )}

        {/* SECCI√ìN PANTALLA (Simplificada) */}
        {activeSection === 'display' && (
            <div className="space-y-6">
                <h3 className="font-bold text-gray-900 dark:text-white mb-3">Fondo</h3>
                <div className="grid grid-cols-3 gap-2">
                  {/* Reutilizamos wallpapers del contexto anterior */}
                  <button onClick={() => setWallpaper('linear-gradient(135deg, #667eea 0%, #764ba2 100%)')} className="aspect-square rounded bg-gradient-to-br from-blue-400 to-purple-600"/>
                  <button onClick={() => setWallpaper('linear-gradient(135deg, #f093fb 0%, #f5576c 100%)')} className="aspect-square rounded bg-gradient-to-br from-pink-300 to-red-400"/>
                  <button onClick={() => setWallpaper('black')} className="aspect-square rounded bg-black border border-gray-600"/>
                </div>
                <label className="flex items-center justify-between p-3 bg-white dark:bg-gray-800 rounded-lg">
                   <span className="font-medium dark:text-white">Modo Oscuro</span>
                   <input type="checkbox" checked={darkMode} onChange={(e) => setDarkMode(e.target.checked)} className="accent-blue-500 w-5 h-5"/>
                </label>
            </div>
        )}

        {/* SECCI√ìN SEGURIDAD (Reutilizada del c√≥digo anterior, solo aseguramos que pinPad funcione) */}
        {activeSection === 'credentials' && (
          <div className="space-y-4">
             {(!pin || !isPinVerified) ? (
               <div className="text-center py-8">
                 <Lock size={40} className="mx-auto text-gray-300 mb-4"/>
                 <button onClick={handleAccessCredentials} className="bg-blue-500 text-white px-6 py-2 rounded-full">
                   {pin ? 'Desbloquear' : 'Crear PIN'}
                 </button>
               </div>
             ) : (
               <div className="text-center py-4">
                 <p className="text-green-500 mb-4">B√≥veda Desbloqueada</p>
                 <button onClick={() => verifyPin(false)} className="text-red-500 text-sm">Bloquear ahora</button>
               </div>
             )}
          </div>
        )}
      </div>

      {showPinPad && (
        <PinPad
          savedPin={pin}
          isSettingNew={pinMode === 'create'}
          onSetPin={setPin}
          onSuccess={() => { verifyPin(true); setShowPinPad(false); }}
          onCancel={() => setShowPinPad(false)}
        />
      )}
    </div>
  )
}