'use client'

import React, { createContext, useContext, useState, useCallback, useEffect } from 'react'

export type OSTheme = 'android' | 'ios' | 'pc'

export interface InstalledApp {
  id: string
  name: string
  url?: string
  favicon?: string
  isNative: boolean
}

export interface Notification {
  id: string
  title: string
  message: string
  timestamp: Date
  read: boolean
  appId?: string
}

export interface Photo {
  id: string
  dataUrl: string
  timestamp: Date
}

export interface Contact {
  id: string
  name: string
  number: string
}

export interface Message {
  id: string
  contactId?: string // ID del contacto si est√° agendado
  number: string // N√∫mero de tel√©fono (identificador)
  text: string
  time: string
  isOwn: boolean // Si lo envi√© yo o lo recib√≠
}

export interface WebAppCredentials {
  [appId: string]: {
    username?: string
    password?: string
  }
}

export interface Network {
  ssid: string
  strength: number
  isSecured: boolean
}

interface OSContextType {
  theme: OSTheme
  setTheme: (theme: OSTheme) => void
  wallpaper: string
  setWallpaper: (url: string) => void
  installedApps: InstalledApp[]
  installApp: (app: InstalledApp) => void
  uninstallApp: (appId: string) => void
  activeApp: string | null
  setActiveApp: (appId: string | null) => void
  previousApp: string | null
  goBack: () => void
  notifications: Notification[]
  addNotification: (notification: Omit<Notification, 'id' | 'timestamp' | 'read'>) => void
  markNotificationRead: (id: string) => void
  darkMode: boolean
  setDarkMode: (value: boolean) => void
  photos: Photo[]
  addPhoto: (dataUrl: string) => void
  contacts: Contact[]
  addContact: (contact: Omit<Contact, 'id'>) => void
  messages: Message[]
  addMessage: (message: Omit<Message, 'id'>) => void
  clearMessages: () => void
  credentials: WebAppCredentials
  setCredentials: (appId: string, creds: WebAppCredentials[string]) => void
  getCredentials: (appId: string) => WebAppCredentials[string] | undefined
  pin: string | null
  setPin: (pin: string) => void
  isPinVerified: boolean
  verifyPin: (success: boolean) => void
  // Redes
  wifiEnabled: boolean
  setWifiEnabled: (enabled: boolean) => void
  connectedNetwork: string | null
  connectToNetwork: (ssid: string) => void
  availableNetworks: Network[]
  scanNetworks: () => void
  // Tel√©fono
  incomingCall: { number: string; name?: string } | null
  setIncomingCall: (call: { number: string; name?: string } | null) => void
}

const OSContext = createContext<OSContextType | undefined>(undefined)

export const OSProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [theme, setTheme] = useState<OSTheme>('android')
  const [wallpaper, setWallpaper] = useState('linear-gradient(135deg, #667eea 0%, #764ba2 100%)')
  const [darkMode, setDarkMode] = useState(true)
  const [activeApp, setActiveApp] = useState<string | null>(null)
  const [previousApp, setPreviousApp] = useState<string | null>(null)
  const [notifications, setNotifications] = useState<Notification[]>([])
  const [photos, setPhotos] = useState<Photo[]>([])
  
  // Comunicaci√≥n
  const [contacts, setContacts] = useState<Contact[]>([])
  const [messages, setMessages] = useState<Message[]>([])
  const [incomingCall, setIncomingCall] = useState<{ number: string; name?: string } | null>(null)

  // Red y Seguridad
  const [credentials, setCredentialsState] = useState<WebAppCredentials>({})
  const [pin, setPinState] = useState<string | null>(null)
  const [isPinVerified, setIsPinVerified] = useState(false)
  const [wifiEnabled, setWifiEnabled] = useState(true)
  const [connectedNetwork, setConnectedNetwork] = useState<string | null>(null)
  const [availableNetworks, setAvailableNetworks] = useState<Network[]>([])

  // Apps
  const [installedApps, setInstalledApps] = useState<InstalledApp[]>([
    { id: 'calculator', name: 'Calculadora', isNative: true },
    { id: 'calendar', name: 'Calendario', isNative: true },
    { id: 'messages', name: 'Mensajes', isNative: true },
    { id: 'phone', name: 'Llamadas', isNative: true },
    { id: 'files', name: 'Archivos', isNative: true },
    { id: 'camera', name: 'C√°mara', isNative: true },
    { id: 'gallery', name: 'Galer√≠a', isNative: true },
    { id: 'settings', name: 'Configuraci√≥n', isNative: true },
    { id: 'store', name: 'Store', isNative: true },
  ])

  // Canal de comunicaci√≥n simulado (entre pesta√±as)
  useEffect(() => {
    const channel = new BroadcastChannel('web-os-network')
    
    channel.onmessage = (event) => {
      const { type, data } = event.data
      
      if (type === 'chat') {
        // Simular recepci√≥n de mensaje
        const knownContact = contacts.find(c => c.number === data.from)
        const newMessage: Message = {
          id: Math.random().toString(36).substr(2, 9),
          number: data.from,
          text: data.text,
          time: new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }),
          isOwn: false,
          contactId: knownContact?.id
        }
        setMessages(prev => [...prev, newMessage])
        addNotification({
          title: knownContact ? knownContact.name : data.from,
          message: data.text,
          appId: 'messages'
        })
      } else if (type === 'call') {
        const knownContact = contacts.find(c => c.number === data.from)
        setIncomingCall({ number: data.from, name: knownContact?.name })
      }
    }

    return () => channel.close()
  }, [contacts])

  // Persistencia
  useEffect(() => {
    if (typeof window !== 'undefined') {
      const savedPin = localStorage.getItem('os_pin')
      if (savedPin) setPinState(savedPin)
      
      const savedContacts = localStorage.getItem('os_contacts')
      if (savedContacts) setContacts(JSON.parse(savedContacts))

      const savedApps = localStorage.getItem('os_apps')
      if (savedApps) {
        try {
           const parsedApps = JSON.parse(savedApps)
           setInstalledApps(prev => {
             const nativeIds = prev.filter(a => a.isNative).map(a => a.id)
             const customApps = parsedApps.filter((a: InstalledApp) => !nativeIds.includes(a.id))
             return [...prev, ...customApps]
           })
        } catch(e) {}
      }
      const savedCreds = localStorage.getItem('os_creds')
      if (savedCreds) setCredentialsState(JSON.parse(savedCreds))
    }
  }, [])

  // Helpers Redes
  const scanNetworks = useCallback(() => {
    // Simulaci√≥n de escaneo
    setAvailableNetworks([])
    setTimeout(() => {
      setAvailableNetworks([
        { ssid: 'Casa_WiFi', strength: 3, isSecured: true },
        { ssid: 'Oficina_5G', strength: 3, isSecured: true },
        { ssid: 'Public_Free', strength: 2, isSecured: false },
        { ssid: 'Vecino_Robar', strength: 1, isSecured: true },
      ])
    }, 1500)
  }, [])

  const connectToNetwork = useCallback((ssid: string) => {
    setConnectedNetwork(ssid)
    addNotification({
      title: 'Wi-Fi Conectado',
      message: `Conectado a ${ssid}`,
      appId: 'settings'
    })
  }, [])

  // Helpers Contactos y Mensajes
  const addContact = useCallback((contact: Omit<Contact, 'id'>) => {
    const newContact = { ...contact, id: Math.random().toString(36).substr(2, 9) }
    setContacts(prev => {
      const updated = [...prev, newContact]
      localStorage.setItem('os_contacts', JSON.stringify(updated))
      return updated
    })
  }, [])

  const addMessage = useCallback((message: Omit<Message, 'id'>) => {
    const newMessage = { ...message, id: Math.random().toString(36).substr(2, 9) }
    setMessages(prev => [...prev, newMessage])
    
    // Si es mi mensaje, enviarlo por el canal a "otras pesta√±as"
    if (message.isOwn) {
      const channel = new BroadcastChannel('web-os-network')
      // Simulamos que mi numero es "123456"
      channel.postMessage({ 
        type: 'chat', 
        data: { from: '123456', to: message.number, text: message.text } 
      })
      channel.close()
    }
  }, [])

  // Resto de helpers (iguales)
  const setPin = useCallback((newPin: string) => {
    setPinState(newPin)
    localStorage.setItem('os_pin', newPin)
    setIsPinVerified(true)
  }, [])

  const installApp = useCallback((app: InstalledApp) => {
    setInstalledApps((prev) => {
      if (!prev.find((a) => a.id === app.id)) {
        const newApps = [...prev, app]
        localStorage.setItem('os_apps', JSON.stringify(newApps))
        return newApps
      }
      return prev
    })
  }, [])

  const uninstallApp = useCallback((appId: string) => {
    setInstalledApps((prev) => {
      const newApps = prev.filter((a) => a.id !== appId)
      localStorage.setItem('os_apps', JSON.stringify(newApps))
      return newApps
    })
  }, [])

  const handleSetActiveApp = useCallback((appId: string | null) => {
    if (appId && appId !== activeApp) {
      setPreviousApp(activeApp)
    }
    setActiveApp(appId)
  }, [activeApp])

  const goBack = useCallback(() => {
    if (previousApp) {
      setActiveApp(previousApp)
      setPreviousApp(null)
    } else {
      setActiveApp(null)
    }
  }, [previousApp])

  const addNotification = useCallback((notification: Omit<Notification, 'id' | 'timestamp' | 'read'>) => {
    const id = Math.random().toString(36).substr(2, 9)
    const newNotification: Notification = {
      ...notification,
      id,
      timestamp: new Date(),
      read: false,
    }
    setNotifications((prev) => [newNotification, ...prev])
    // Auto cerrar notificaci√≥n visual flotante (toast), pero mantener en centro de notificaciones
    // Aqu√≠ asumo que el UI maneja el toast separado del array principal si se desea
  }, [])

  const markNotificationRead = useCallback((id: string) => {
    setNotifications((prev) =>
      prev.map((n) => (n.id === id ? { ...n, read: true } : n))
    )
  }, [])

  const addPhoto = useCallback((dataUrl: string) => {
    const photo: Photo = { id: Math.random().toString(36).substr(2, 9), dataUrl, timestamp: new Date() }
    setPhotos((prev) => [photo, ...prev])
  }, [])

  const clearMessages = useCallback(() => setMessages([]), [])

  const setCredentials = useCallback((appId: string, creds: WebAppCredentials[string]) => {
    setCredentialsState((prev) => {
      const newCreds = { ...prev, [appId]: creds }
      localStorage.setItem('os_creds', JSON.stringify(newCreds))
      return newCreds
    })
  }, [])

  const getCredentials = useCallback((appId: string) => credentials[appId], [credentials])

  return (
    <OSContext.Provider
      value={{
        theme, setTheme, wallpaper, setWallpaper, installedApps, installApp, uninstallApp,
        activeApp, setActiveApp: handleSetActiveApp, previousApp, goBack,
        notifications, addNotification, markNotificationRead, darkMode, setDarkMode,
        photos, addPhoto, messages, addMessage, clearMessages, credentials, setCredentials, getCredentials,
        pin, setPin, isPinVerified, verifyPin,
        // Nuevos
        contacts, addContact,
        wifiEnabled, setWifiEnabled, connectedNetwork, connectToNetwork, availableNetworks, scanNetworks,
        incomingCall, setIncomingCall
      }}
    >
      {children}
    </OSContext.Provider>
  )
}

export const useOS = () => {
  const context = useContext(OSContext)
  if (!context) throw new Error('useOS must be used within OSProvider')
  return context
}