'use client'

import { useState } from 'react'
import { useOS } from '@/context/os-context'

export const Messages = () => {
  const { messages, addMessage } = useOS()
  const [input, setInput] = useState('')

  const sendMessage = () => {
    if (input.trim()) {
      addMessage({
        name: 'T√∫',
        text: input,
        time: new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }),
      })
      setInput('')
    }
  }

  return (
    <div className="w-full h-full flex flex-col bg-white dark:bg-gray-900">
      <div className="flex-1 overflow-y-auto p-4 space-y-4">
        {messages.map((msg) => (
          <div key={msg.id} className={`flex ${msg.name === 'T√∫' ? 'justify-end' : 'justify-start'}`}>
            <div className={`max-w-xs px-4 py-2 rounded-2xl ${msg.name === 'T√∫' ? 'bg-blue-500 text-white' : 'bg-gray-200 text-gray-900 dark:bg-gray-700 dark:text-gray-100'}`}>
              <p className="text-sm">{msg.text}</p>
              <span className="text-xs opacity-70">{msg.time}</span>
            </div>
          </div>
        ))}
      </div>
      <div className="p-4 border-t dark:border-gray-700 flex gap-2">
        <input
          type="text"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          onKeyPress={(e) => e.key === 'Enter' && sendMessage()}
          placeholder="Mensaje..."
          className="flex-1 px-4 py-2 border rounded-full focus:outline-none focus:ring-2 focus:ring-blue-500 dark:bg-gray-700 dark:text-white dark:border-gray-600"
        />
        <button
          onClick={sendMessage}
          className="px-6 py-2 bg-blue-500 text-white rounded-full hover:bg-blue-600"
        >
          Enviar
        </button>
      </div>
    </div>
  )
}
