'use client'

import { useOS } from '@/context/os-context'
import { HomeScreen } from '@/components/screens/home-screen'
import { AppWindow } from '@/components/app-window'
import { Taskbar } from '@/components/taskbar'
import { useEffect, useState, useRef } from 'react'
import { ChevronDown } from 'lucide-react'

const FRAME_SIZES = {
  android: { width: 360, height: 800 },
  ios: { width: 390, height: 844 },
  pc: { width: 1024, height: 768 },
}

export const MobileFrame = () => {
  const { theme, activeApp, darkMode, notifications } = useOS()
  const [mounted, setMounted] = useState(false)
  const [notificationsOpen, setNotificationsOpen] = useState(false)
  const [touchStart, setTouchStart] = useState(0)
  const notificationsPanelRef = useRef<HTMLDivElement>(null)

  const size = FRAME_SIZES[theme]

  useEffect(() => {
    setMounted(true)
  }, [])

  const handleTouchStart = (e: React.TouchEvent) => {
    if (e.touches[0].clientY < 50) {
      setTouchStart(e.touches[0].clientY)
    }
  }

  const handleTouchMove = (e: React.TouchEvent) => {
    if (touchStart > 0 && e.touches[0].clientY > touchStart + 50) {
      setNotificationsOpen(true)
    }
  }

  const handleTouchEnd = () => {
    setTouchStart(0)
  }

  if (!mounted) return null

  const getFrameStyle = () => {
    if (theme === 'pc') {
      return 'rounded-2xl border-8 border-gray-700 shadow-2xl'
    }
    if (theme === 'ios') {
      return 'rounded-[40px] border-[10px] border-gray-900 shadow-2xl'
    }
    return 'rounded-3xl border-8 border-gray-800 shadow-2xl'
  }

  return (
    <div
      className={`relative overflow-hidden bg-black ${getFrameStyle()}`}
      style={{
        width: size.width,
        height: size.height,
      }}
      onTouchStart={handleTouchStart}
      onTouchMove={handleTouchMove}
      onTouchEnd={handleTouchEnd}
    >
      {theme === 'ios' && (
        <div className="absolute top-0 left-1/2 transform -translate-x-1/2 w-40 h-8 bg-black rounded-b-3xl z-50 flex items-center justify-between px-4">
          <span className="text-white text-xs">9:41</span>
          <span className="text-white text-xs">üì∂</span>
        </div>
      )}

      {theme === 'android' && (
        <div className="absolute top-0 left-0 right-0 h-7 bg-black border-b border-gray-800 flex items-center justify-between px-4 z-40">
          <span className="text-white text-xs">9:41</span>
          <div className="flex gap-1 items-center">
            {notifications.length > 0 && (
              <span className="text-yellow-400 text-xs">üîî {notifications.length}</span>
            )}
            <span className="text-white text-xs">üì∂ üîã</span>
          </div>
        </div>
      )}

      {notificationsOpen && (
        <div 
          ref={notificationsPanelRef}
          className="absolute top-7 left-0 right-0 bg-black border-b border-gray-800 z-30 max-h-60 overflow-y-auto"
        >
          <div className="p-4">
            <div className="flex items-center justify-between mb-4">
              <h3 className="text-white font-semibold text-sm">Notificaciones</h3>
              <button
                onClick={() => setNotificationsOpen(false)}
                className="text-gray-400 hover:text-white"
              >
                <ChevronDown size={20} />
              </button>
            </div>
            {notifications.length === 0 ? (
              <p className="text-gray-500 text-xs">Sin notificaciones</p>
            ) : (
              <div className="space-y-2">
                {notifications.map((notif) => (
                  <div key={notif.id} className="bg-gray-900 p-2 rounded border border-gray-800">
                    <p className="text-white text-xs font-semibold">{notif.title}</p>
                    <p className="text-gray-400 text-xs mt-1">{notif.message}</p>
                    <p className="text-gray-600 text-xs mt-1">
                      {notif.timestamp.toLocaleTimeString()}
                    </p>
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>
      )}

      <div
        className={`w-full h-full overflow-hidden flex flex-col`}
        style={{
          marginTop: theme === 'ios' ? 32 : theme === 'android' ? 28 : 0,
        }}
      >
        {activeApp ? <AppWindow appId={activeApp} /> : <HomeScreen />}
      </div>

      <Taskbar />
    </div>
  )
}
