'use client'

import { useOS } from '@/context/os-context'
import { InstalledApp } from '@/context/os-context'
import { KeyRound, ShieldCheck } from 'lucide-react'
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog"
import { useState, useEffect } from 'react'

interface WebBrowserProps {
  app: InstalledApp
}

export const WebBrowser = ({ app }: WebBrowserProps) => {
  const { credentials, setCredentials, getCredentials } = useOS()
  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')
  const [isCredsOpen, setIsCredsOpen] = useState(false)
  const [showPassword, setShowPassword] = useState(false)

  // Cargar credenciales al abrir o cambiar de app
  useEffect(() => {
    const creds = getCredentials(app.id)
    setUsername(creds?.username || '')
    setPassword(creds?.password || '')
  }, [app.id, getCredentials])

  const handleSave = () => {
    setCredentials(app.id, { username, password })
    setIsCredsOpen(false)
  }

  const hasCreds = !!getCredentials(app.id)?.username

  return (
    <div className="w-full h-full flex flex-col bg-black">
      {/* Top bar */}
      <div className="bg-gray-900 border-b border-gray-800 p-3 space-y-2 shadow-sm z-10">
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-2">
            <div className="w-6 h-6 bg-white/10 rounded flex items-center justify-center overflow-hidden">
              <img
                src={app.favicon || "/placeholder.svg"}
                alt={app.name}
                className="w-4 h-4 object-contain"
                onError={(e) => {
                  e.currentTarget.style.display = 'none'
                }}
              />
            </div>
            <span className="text-white font-semibold text-sm">{app.name}</span>
          </div>
          
          <Dialog open={isCredsOpen} onOpenChange={setIsCredsOpen}>
            <DialogTrigger asChild>
              <button 
                className={`p-1.5 rounded-lg transition-all active:scale-95 ${
                  hasCreds 
                    ? 'bg-green-500/20 text-green-400 hover:bg-green-500/30' 
                    : 'bg-gray-800 text-gray-400 hover:bg-gray-700 hover:text-white'
                }`}
                title="Gestor de contrase√±as"
              >
                {hasCreds ? <ShieldCheck size={16} /> : <KeyRound size={16} />}
              </button>
            </DialogTrigger>
            <DialogContent className="w-[90%] max-w-[320px] rounded-2xl bg-white dark:bg-gray-900 border-gray-200 dark:border-gray-800 p-6">
              <DialogHeader className="mb-4">
                <DialogTitle className="text-gray-900 dark:text-white flex items-center gap-2 text-lg">
                  <span className="p-2 bg-blue-100 dark:bg-blue-900/30 rounded-lg text-blue-600 dark:text-blue-400">
                    <KeyRound size={20} />
                  </span>
                  B√≥veda: {app.name}
                </DialogTitle>
              </DialogHeader>
              <div className="space-y-4">
                <div className="space-y-1.5">
                  <label className="text-xs font-medium text-gray-500 dark:text-gray-400 ml-1">Usuario</label>
                  <input
                    value={username}
                    onChange={(e) => setUsername(e.target.value)}
                    className="w-full px-4 py-3 bg-gray-50 dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-xl dark:text-white text-sm focus:ring-2 focus:ring-blue-500 outline-none"
                    placeholder="ejemplo@email.com"
                  />
                </div>
                <div className="space-y-1.5">
                  <label className="text-xs font-medium text-gray-500 dark:text-gray-400 ml-1">Contrase√±a</label>
                  <div className="relative">
                    <input
                      value={password}
                      onChange={(e) => setPassword(e.target.value)}
                      type={showPassword ? "text" : "password"}
                      className="w-full px-4 py-3 bg-gray-50 dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-xl dark:text-white text-sm focus:ring-2 focus:ring-blue-500 outline-none pr-10"
                      placeholder="‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢"
                    />
                    <button 
                      onClick={() => setShowPassword(!showPassword)}
                      className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-400 hover:text-gray-600 dark:hover:text-gray-200"
                    >
                      {showPassword ? "Ocultar" : "Ver"}
                    </button>
                  </div>
                </div>
                <button 
                  onClick={handleSave}
                  className="w-full bg-blue-600 text-white py-3 rounded-xl hover:bg-blue-700 font-bold mt-2 shadow-lg shadow-blue-500/20 active:scale-[0.98] transition-transform"
                >
                  Guardar Credenciales
                </button>
              </div>
            </DialogContent>
          </Dialog>
        </div>

        <div className="bg-gray-800 rounded-lg px-3 py-1.5 text-xs text-gray-400 truncate font-mono flex items-center gap-2">
          <span className="text-green-500">üîí</span>
          {app.url ? new URL(app.url).hostname : 'local://app'}
        </div>
      </div>

      <div className="flex-1 overflow-hidden bg-white relative">
        {app.url ? (
          <iframe
            src={app.url}
            className="w-full h-full border-0"
            title={app.name}
            // Permisos necesarios para que funcionen la mayor√≠a de webs modernas
            sandbox="allow-same-origin allow-scripts allow-popups allow-forms allow-presentation"
            allow="camera; microphone; geolocation"
          />
        ) : (
          <div className="w-full h-full flex items-center justify-center text-gray-400 bg-gray-50 dark:bg-gray-900">
            <div className="text-center p-8">
              <div className="text-6xl mb-4 animate-pulse">üåê</div>
              <p className="text-lg font-medium text-gray-600 dark:text-gray-300">{app.name}</p>
              <p className="text-sm mt-2 text-gray-400">URL no configurada correctamente</p>
            </div>
          </div>
        )}
      </div>
    </div>
  )
}