'use client'

import { useState, useEffect } from 'react'
import { Delete, Check, X } from 'lucide-react'

interface PinPadProps {
  onSuccess: () => void
  savedPin: string | null
  onSetPin: (pin: string) => void
  isSettingNew: boolean
  onCancel: () => void
}

export const PinPad = ({ onSuccess, savedPin, onSetPin, isSettingNew, onCancel }: PinPadProps) => {
  const [input, setInput] = useState('')
  const [confirmInput, setConfirmInput] = useState('')
  const [step, setStep] = useState<'enter' | 'create' | 'confirm'>('enter')
  const [error, setError] = useState(false)
  const [message, setMessage] = useState('')

  useEffect(() => {
    if (isSettingNew) {
      setStep('create')
      setMessage('Crea un nuevo PIN')
    } else {
      setStep('enter')
      setMessage('Introduce tu PIN')
    }
  }, [isSettingNew])

  const handleNum = (num: string) => {
    if (error) return
    const currentInput = step === 'confirm' ? confirmInput : input
    
    if (currentInput.length < 4) {
      if (step === 'confirm') {
        setConfirmInput(prev => prev + num)
      } else {
        setInput(prev => prev + num)
      }
    }
  }

  const handleDelete = () => {
    if (step === 'confirm') {
      setConfirmInput(prev => prev.slice(0, -1))
    } else {
      setInput(prev => prev.slice(0, -1))
    }
    setError(false)
  }

  const handleSubmit = () => {
    if (step === 'enter') {
      if (input === savedPin) {
        onSuccess()
      } else {
        showError('PIN Incorrecto')
        setInput('')
      }
    } else if (step === 'create') {
      if (input.length === 4) {
        setStep('confirm')
        setMessage('Confirma tu nuevo PIN')
      }
    } else if (step === 'confirm') {
      if (confirmInput === input) {
        onSetPin(input)
        onSuccess()
      } else {
        showError('Los PIN no coinciden')
        setConfirmInput('')
        setStep('create')
        setInput('')
        setMessage('Crea un nuevo PIN')
      }
    }
  }

  const showError = (msg: string) => {
    setError(true)
    const originalMsg = message
    setMessage(msg)
    setTimeout(() => {
      setError(false)
      setMessage(originalMsg)
    }, 1500)
  }

  const activeInput = step === 'confirm' ? confirmInput : input

  return (
    <div className="fixed inset-0 bg-black/90 backdrop-blur-md z-[100] flex items-center justify-center p-4 animate-in fade-in duration-200">
      <div className="bg-white dark:bg-gray-900 p-8 rounded-3xl w-full max-w-xs shadow-2xl border border-gray-200 dark:border-gray-800">
        <div className="flex justify-end mb-2">
          <button onClick={onCancel} className="text-gray-400 hover:text-gray-600 dark:hover:text-white">
            <X size={24} />
          </button>
        </div>
        
        <h3 className={`text-center text-xl font-bold mb-8 transition-colors ${error ? 'text-red-500' : 'text-gray-900 dark:text-white'}`}>
          {message}
        </h3>

        <div className="flex justify-center gap-6 mb-10">
          {[0, 1, 2, 3].map((i) => (
            <div
              key={i}
              className={`w-4 h-4 rounded-full transition-all duration-200 ${
                i < activeInput.length 
                  ? error ? 'bg-red-500 scale-110' : 'bg-blue-500 scale-110' 
                  : 'bg-gray-200 dark:bg-gray-800 border border-gray-300 dark:border-gray-700'
              }`}
            />
          ))}
        </div>

        <div className="grid grid-cols-3 gap-4 mb-8">
          {[1, 2, 3, 4, 5, 6, 7, 8, 9].map((num) => (
            <button
              key={num}
              onClick={() => handleNum(num.toString())}
              className="aspect-square rounded-full bg-gray-100 dark:bg-gray-800/50 hover:bg-gray-200 dark:hover:bg-gray-700 text-2xl font-medium transition-all active:scale-95 text-gray-900 dark:text-white"
            >
              {num}
            </button>
          ))}
          <div /> {/* Spacer */}
          <button
            onClick={() => handleNum('0')}
            className="aspect-square rounded-full bg-gray-100 dark:bg-gray-800/50 hover:bg-gray-200 dark:hover:bg-gray-700 text-2xl font-medium transition-all active:scale-95 text-gray-900 dark:text-white"
          >
            0
          </button>
          <button 
            onClick={handleDelete} 
            className="aspect-square flex items-center justify-center text-gray-500 hover:text-red-500 transition-colors active:scale-95"
          >
            <Delete size={28} />
          </button>
        </div>

        <button
          onClick={handleSubmit}
          disabled={activeInput.length !== 4}
          className="w-full py-4 bg-blue-600 disabled:opacity-50 disabled:cursor-not-allowed text-white rounded-2xl font-bold hover:bg-blue-700 transition-all flex items-center justify-center gap-2 active:scale-[0.98]"
        >
          {step === 'create' ? 'Siguiente' : 'Confirmar'} <Check size={20} />
        </button>
      </div>
    </div>
  )
}