'use client'

import { useState, useRef } from 'react'
import { useOS } from '@/context/os-context'

export const Camera = () => {
  const videoRef = useRef<HTMLVideoElement>(null)
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const [isActive, setIsActive] = useState(false)
  const { addPhoto, addNotification } = useOS()

  const startCamera = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ video: true })
      if (videoRef.current) {
        videoRef.current.srcObject = stream
        setIsActive(true)
      }
    } catch (err) {
      addNotification({
        title: 'Error',
        message: 'No se puede acceder a la c√°mara',
      })
    }
  }

  const takePhoto = () => {
    if (videoRef.current && canvasRef.current) {
      const context = canvasRef.current.getContext('2d')
      if (context) {
        context.drawImage(videoRef.current, 0, 0, canvasRef.current.width, canvasRef.current.height)
        const photo = canvasRef.current.toDataURL('image/jpeg')
        addPhoto(photo)
        addNotification({
          title: 'Foto guardada',
          message: 'La foto se guard√≥ en la galer√≠a',
        })
      }
    }
  }

  const stopCamera = () => {
    if (videoRef.current?.srcObject) {
      const tracks = (videoRef.current.srcObject as MediaStream).getTracks()
      tracks.forEach((track) => track.stop())
      setIsActive(false)
    }
  }

  return (
    <div className="w-full h-full flex flex-col bg-black">
      {!isActive ? (
        <div className="flex-1 flex flex-col items-center justify-center gap-4">
          <div className="text-6xl">üì∑</div>
          <button
            onClick={startCamera}
            className="px-8 py-3 bg-blue-500 text-white rounded-lg hover:bg-blue-600 font-semibold"
          >
            Abrir C√°mara
          </button>
        </div>
      ) : (
        <>
          <video
            ref={videoRef}
            autoPlay
            playsInline
            className="flex-1 w-full object-cover"
          />
          <canvas ref={canvasRef} className="hidden" width={400} height={300} />
          <div className="p-4 flex gap-2">
            <button
              onClick={takePhoto}
              className="flex-1 px-4 py-3 bg-green-500 text-white rounded-lg hover:bg-green-600 font-semibold"
            >
              üì∏ Captura
            </button>
            <button
              onClick={stopCamera}
              className="flex-1 px-4 py-3 bg-red-500 text-white rounded-lg hover:bg-red-600 font-semibold"
            >
              Cerrar
            </button>
          </div>
        </>
      )}
    </div>
  )
}
