'use client'

import { useOS } from '@/context/os-context'
import { useState } from 'react'

interface FileItem {
  id: string
  name: string
  type: 'folder' | 'image' | 'video' | 'audio' | 'document'
  size: string
  category: 'music' | 'video' | 'images' | 'documents' | 'cache'
}

export const Files = () => {
  const { photos } = useOS()
  const [activeCategory, setActiveCategory] = useState<string>('all')

  const allFiles: FileItem[] = [
    // Photos from camera
    ...photos.map((photo, idx) => ({
      id: `photo-${photo.id}`,
      name: `Foto ${idx + 1}`,
      type: 'image' as const,
      size: '2.4 MB',
      category: 'images' as const,
    })),
    // Music files
    { id: 'm1', name: 'cancion.mp3', type: 'audio', size: '5.2 MB', category: 'music' },
    { id: 'm2', name: 'musica.wav', type: 'audio', size: '12.1 MB', category: 'music' },
    // Videos
    { id: 'v1', name: 'video.mp4', type: 'video', size: '45.3 MB', category: 'video' },
    { id: 'v2', name: 'clip.mkv', type: 'video', size: '78.5 MB', category: 'video' },
    // Documents
    { id: 'd1', name: 'reporte.pdf', type: 'document', size: '2.8 MB', category: 'documents' },
    { id: 'd2', name: 'notas.txt', type: 'document', size: '0.5 MB', category: 'documents' },
    // Cache
    { id: 'c1', name: 'cache_app1', type: 'folder', size: '156 MB', category: 'cache' },
    { id: 'c2', name: 'cache_app2', type: 'folder', size: '89 MB', category: 'cache' },
  ]

  const categories = [
    { id: 'all', name: 'Todo', icon: 'üìÇ' },
    { id: 'music', name: 'M√∫sica', icon: 'üéµ' },
    { id: 'video', name: 'Videos', icon: 'üé¨' },
    { id: 'images', name: 'Im√°genes', icon: 'üñºÔ∏è' },
    { id: 'documents', name: 'Documentos', icon: 'üìÑ' },
    { id: 'cache', name: 'Cach√©', icon: '‚öôÔ∏è' },
  ]

  const filteredFiles = activeCategory === 'all' 
    ? allFiles 
    : allFiles.filter(f => f.category === activeCategory)

  const getFileIcon = (type: string) => {
    switch (type) {
      case 'image': return 'üñºÔ∏è'
      case 'video': return 'üé¨'
      case 'audio': return 'üéµ'
      case 'document': return 'üìÑ'
      case 'folder': return 'üìÅ'
      default: return 'üìÑ'
    }
  }

  return (
    <div className="w-full h-full flex flex-col bg-gray-900">
      <div className="flex gap-2 p-3 overflow-x-auto bg-gray-800 border-b border-gray-700">
        {categories.map((cat) => (
          <button
            key={cat.id}
            onClick={() => setActiveCategory(cat.id)}
            className={`px-4 py-2 rounded-lg whitespace-nowrap text-sm font-semibold transition-all flex items-center gap-2 ${
              activeCategory === cat.id
                ? 'bg-blue-500 text-white'
                : 'bg-gray-700 text-gray-300 hover:bg-gray-600'
            }`}
          >
            <span>{cat.icon}</span>
            {cat.name}
          </button>
        ))}
      </div>

      {/* Files grid */}
      <div className="flex-1 overflow-y-auto p-4">
        {filteredFiles.length === 0 ? (
          <div className="flex items-center justify-center h-full">
            <div className="text-center text-gray-400">
              <p className="text-2xl mb-2">üì≠</p>
              <p className="text-sm">Sin archivos en esta categor√≠a</p>
            </div>
          </div>
        ) : (
          <div className="grid grid-cols-2 gap-3">
            {filteredFiles.map((file) => (
              <div
                key={file.id}
                className="bg-gray-800 rounded-lg p-4 hover:bg-gray-700 transition-colors cursor-pointer group"
              >
                <div className="text-4xl mb-2 group-hover:scale-110 transition-transform">
                  {getFileIcon(file.type)}
                </div>
                <p className="text-white text-sm font-semibold truncate">{file.name}</p>
                <p className="text-gray-400 text-xs mt-1">{file.size}</p>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  )
}
